.Navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}
.Navbar__Links{
    display: flex;
    flex: 1 1;
    justify-content: flex-start;
    align-items: center;
}
.Navbar__Links-logo{
    margin-top: 2px;
}

.Navbar__Links-container p a:hover{
