    }

    .Navbar__menu{
        display: flex;
    }
}
@media screen and (max-width: 700px) {
    .Navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .Navbar {
        padding: 2rem;
        position: relative;
        z-index: 1000;
    }

   
    .Navbar__menu-container {
        top: 20px;
    }
}
