    /* transform: translateY(-50%); */
    
    text-align: center;
    /* margin-left: 7rem ; */
    /* margin-top: -1rem; */

   
  }
  .middle a {
      text-align: center;
      /* align-items: center; */
  }

  .btn{
    display: inline-block;
    width: 40px;
    height: 40px;
    background: var(--gradient-text-light) !important;
    margin: 10px;
    border-radius: 5px;
    box-shadow:0 5px 15px -5px #b2b2b236;
    color: #3498db;
    overflow: hidden;
    position: relative;
  }
  .btn i{
    line-height: 30px;
    font-size: 26px;
    transition: 0.2s linear;
    margin-left: -5px;
  }
  .btn:hover i{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    color: #f1f1f1;
  }
  .btn::before{
    content: "";
    position: absolute;
    width: 150%;
    height: 150%;
    background: #3498db !important;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    left: -110%;
    top: 100%;
  }
  .btn:hover::before{
    -webkit-animation: aaa 0.7s 1;
            animation: aaa 0.7s 1;
    top: -10%;
    left: -25%;
  }
  @-webkit-keyframes aaa {
    0%{
      left: -150%;
      top: 50%;
    }50%{
      left: -25%;
      top: -30%;
    }100%{
      top: -25%;
      left: -10%;
    }
  }
  @keyframes aaa {
    0%{
      left: -150%;
      top: 50%;
    }50%{
      left: -25%;
      top: -30%;
    }100%{
      top: -25%;
      left: -10%;
    }
  }
  .middle .btn:nth-child(1)::before{
    background-color: #CD486B !important;
  }
  .middle .btn:nth-child(2)::before{
    background-color: #0072B1 !important;
  }
  .middle .btn:nth-child(3)::before{
    background-color: #333 !important;
  }
  
  
    




