.contactForm {
    width: 40%;
    padding: 40px;
    /* background: white;
    box-shadow: 10px 10px 10px 10px #aaaaaa; */
}

.contactForm h2{
    font-size: 40px;
    color:white;
    font-weight: 500;
    margin-bottom: 25px;
}

.contactForm .inputBox {
    position: relative;
    width: 100%;
    margin-top: 10px;
}

.contactForm .inputBox input,
.contactForm .inputBox textarea {
    width: 100%;
    padding: 5px 0;
    font-size: 16px;
    margin: 10px 0;
    position: relative;
    border: none;
    resize: none;
    outline: none;
    border: none;
    background-color: transparent;
    color: #fff;
}
.contactForm .inputBox input:not([type="submit"]),
.contactForm .inputBox textarea{
    border-bottom: 1px solid rgb(255, 75, 43) !important;
    border-radius: 0px;
}

.contactForm .inputBox span {
    position: absolute;
    left: 10px;
    padding: 5px 0;
    font-size: 16px;
    margin: 10px 0;
    pointer-events: none;
    transition: 0.5s;
    color: #666;
}

.contactForm .inputBox input:focus ~ span ,
.contactForm .inputBox input:valid ~ span ,
.contactForm .inputBox textarea:focus ~ span ,
.contactForm .inputBox textarea:valid ~ span

{
    color: #e91e63;
    font-size: 12px;
    -webkit-transform: translateY(-22px);
            transform: translateY(-22px);
    position: absolute;
    left: 0;
}

.contactForm .inputBox button {
    width: 100%;
    background: var(--gradient-text);
    border: none;
    cursor: pointer;
    font-size: 18px;
    color: #fff;
}

@media (max-width: 991px) {
